body {
  --active-link-color: #00acdc;
  margin: 0;
  padding: 0;
}

header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header-nav {
  align-items: stretch;
  height: 100%;
  margin-right: 50px;
}

.header-nav-ul {
  flex-direction: row;
  align-items: stretch;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header-nav-ul-li {
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

.header-nav-ul .header-nav-ul-li:first-child {
  margin-left: 0;
}

.header-nav-ul .header-nav-ul-li:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--active-link-color);
  cursor: pointer;
}

