  justify-content: center;
  align-items: center;
  margin-left: 50px;
  display: flex;
}

.is-active {
  color: var(--active-link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
